<template>
    <div class="hietory-table">
      <base-table :data="history" :columns="columns"
                  :totalSize="totalSize"
                  :pageSizeOpts="pageSizeOpts"
                  :placement="placement"
                  :current="index"
                  @changePage="changePage"
                   @changeTable="changeTable">
        <i-select v-model="model2" size="small" style="width:100px;margin-right: 10px" slot="sort" placeholder="" @on-change="sort">
        <i-option v-for="item in List" :value="item.value" :key="item.value">{{ item.label }}</i-option>
      </i-select>

      </base-table>
    </div>
</template>

<script>
import BaseTable from "../base/base-table.vue"
import {getHistoryUrl,getSortUrl} from  '../common/js/url'
import ISelect from 'iview/src/components/select/select.vue';
import IOption from 'iview/src/components/select/option.vue';

  export default {
  data(){
    return{
      history:[],
      columns:[{
        type: 'selection',
        width: 60,
        align: 'center'
      },
        {
          title: 'qqId',
          key: 'qqid',
          sortable: true
        },
        {
          title: 'myId',
          key: 'myid',
          sortable: true
        },
        {
          title: 'Jitter',
          key: 'jitter',
          sortable: true,
          filters: [
            {
              label: '0.15%',
              value: 1
            },
            {
              label: '0.5%',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.jitter>'0.15%';
            } else if (value === 2) {
              return row.jitter < '0.5%';
            }
          }
        },
        {
          title: 'Shimmer',
          key: 'shimmer',
          sortable: true,
          filters: [
            {
              label: '0.15%',
              value: 1
            },
            {
              label: '0.5%',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.shimmer>'0.15%';
            } else if (value === 2) {
              return row.shimmer < '0.5%';
            }
          }
        },
        {
          title: 'HNR',
          key: 'hnr',
          sortable: true,
          filters: [
            {
              label: '0.15%',
              value: 1
            },
            {
              label: '0.5%',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.hnr>'0.15%';
            } else if (value === 2) {
              return row.hnr < '0.5%';
            }
          }
        },
        {
          title: 'time',
          key: 'time',
          sortable: true,
        }],
      totalSize:0,                      //
      pageSizeOpts:[10,30,50,100],      //
      placement:"top",                  //
      changePageSize:10,                 // /
      List: [
        {
          value: 'default',
          label: ''
        },
        {
          value: 'jitterAsc',
          label: 'Jitter'
        },
        {
          value: 'jitterDes',
          label: 'Jitter'
        },
        {
          value: 'shimmerAsc',
          label: 'Shimmer'
        },
        {
          value: 'shimmerDes',
          label: 'Shimmer'
        },
        {
          value: 'hnrAsc',
          label: 'Hnr'
        },
        {
          value: 'hnrDes',
          label: 'Hnr'
        }
      ],
      model2: '',
      sortData:[{                //
        sort_Directions: '',
        sort_Properties: '',
      }],
      index:1
    }
  },
    created(){
      this.getHistory()
    },
    mounted(){

  },
    methods:{
      getHistory(){
        getHistoryUrl().then((res) =>{             //
          this.history = res.result[0].content
          this.totalSize = res.result[0].totalPages * res.result[0].size
        })
      },
      changePage(PageSize){                        // /
        this.changePageSize = PageSize
      },
      changeTable(index){                         //
        this.index = index
        if(this.sortData[0].sort_Directions == '' || this.sortData[0].sort_Properties == ''){
          getHistoryUrl(index,this.changePageSize).then((res) =>{
            if(res.status == '0'){
              this.history = res.result[0].content
              this.totalSize = res.result[0].totalPages * res.result[0].size
            }else{
             console.log(res.msg)
            }
          })
        }else{
          getSortUrl(index,this.changePageSize,this.sortData[0].sort_Directions,this.sortData[0].sort_Properties).then((res) =>{
            if(res.status == '0') {
              this.history = res.result[0].content
              this.totalSize = res.result[0].totalPages * res.result[0].size
            }else {
              console.log(res.msg)
            }
          })
        }
      },
      sort(value){
        if(value == 'jitterAsc'){
          this.sortData[0].sort_Directions = 'ASC',
            this.sortData[0].sort_Properties = 'jitter'
        }if(value == 'jitterDes'){
          this.sortData[0].sort_Directions = 'DESC',
            this.sortData[0].sort_Properties = 'jitter'
        }
        if(value == 'shimmerAsc'){
          this.sortData[0].sort_Directions = 'ASC',
            this.sortData[0].sort_Properties = 'shimmer'
        }if(value == 'shimmerDes'){
          this.sortData[0].sort_Directions = 'DESC',
            this.sortData[0].sort_Properties = 'shimmer'
        }
        if(value == 'hnrAsc'){
          this.sortData[0].sort_Directions = 'ASC',
            this.sortData[0].sort_Properties = 'hnr'
        }if(value == 'hnrDes'){
          this.sortData[0].sort_Directions = 'DESC',
            this.sortData[0].sort_Properties = 'hnr'
        }if(value == 'default'){
          this.sortData[0].sort_Directions = '',
            this.sortData[0].sort_Properties = ''
          this.index = 1
          getHistoryUrl(1,this.changePageSize).then((res) =>{
            if(res.status == '0'){
            this.history = res.result[0].content
            this.totalSize = res.result[0].totalPages * res.result[0].size
            }else {
              console.log(res.msg)
            }
          })
        }
        else{
          getSortUrl(this.index,this.changePageSize,this.sortData[0].sort_Directions,this.sortData[0].sort_Properties).then((res) =>{
            if(res.status == '0'){
            this.history = res.result[0].content
            this.totalSize = res.result[0].totalPages * res.result[0].size
            }else {
              console.log(res.msg)
            }
          })
          return
        }
      }
    },
    watch:{
      //changePageSize
      changePageSize(){
        this.changeTable(1)
      }
    },
  components:{
    BaseTable,
    ISelect,
    IOption
  }
  }
</script>
<style lang="scss">

  .ivu-select-selected-value{
    width: 90px;
  }
</style>



